<template>

    <div id="login">

        <div class="loginToHome">
            <el-form
                    ref="form"
                    :model="form"
                    :rules="ruleForm"
                    status-icon
                    label-width="80px"
                    class="loginForm"
            >
                <h3 style="margin-bottom: 30px; margin-top: 20px">登录</h3>
                <el-form-item
                        label="用户名"
                        prop="name"
                >
                    <el-input
                            type="text"
                            v-model="form.name"
                            auto-complete="off"
                            placeholder="请输入用户名"
                    ></el-input>
                </el-form-item>
                <el-form-item
                        label="密码"
                        prop="password"
                >
                    <el-input
                            type="password"
                            v-model="form.password"
                            auto-complete="off"
                            placeholder="请输入密码"
                    ></el-input>
                </el-form-item>
                <el-form-item>
                    <el-button
                            class="homeBut"
                            type="primary"
                            plain
                            @click="submit"

                    >登录
                    </el-button>
                    <el-button
                            class="loginBut"
                            type="primary"
                            plain
                            @click="resetForm()"
                    >重置
                    </el-button>
                </el-form-item>
            </el-form>
        </div>

    </div>

</template>

<script>
    import {request} from '../../network/request'
    import Sidentify from "./SIdentify"
    import {adminlogin} from '@/api/user';
    export default {
        data() {
            return {

                form: {
                    name: '',
                    password: '',
                },
                ruleForm: {
                    name: [
                        {required: true, message: '请输入账号', trigger: 'blur'},
                    ],
                    password: [
                        {required: true, message: '请输入密码', trigger: 'blur'},
                    ],

                },
                code: ""
            }

        },
        methods: {
            toRegister() {
                this.$router.replace("/register");
            },
            submit() {
              adminlogin(this.form.name,this.form.password).then((response)=>{
                if(response.data.code === 0){
                    sessionStorage.setItem("id",response.data.data.id),
                      sessionStorage.setItem("username",response.data.data.username),
                      this.$message.success("登录成功")
                      this.$router.push('./video')
                  location.reload();
                    this.$router.go(1);
                }else{
                  sessionStorage.setItem("username","登录"),
                  this.$message.error(response.data.msg);
                }
              })
            },
            resetForm() {
                this.$refs.form.resetFields();
            },

        },
        components: {
            'v-sidentify': Sidentify
        },
        created() {


        }
    }

</script>

<style scoped>
    .loginToHome {
        position: absolute;
        left: 0px;
        right: 0;
        top: -100px;
        bottom: 0;
        margin: auto;
        width: 400px;
        height: 350px;
        -webkit-border-radius: 5px;
        border-radius: 5px;
        background: #fff;
        border: 1px solid #dcdfe6;
        box-shadow: 2px 2px 3px #dcdfe6;
    }

    .loginForm {
        text-align: center;
        padding-top: 15px;
        padding-right: 30px;
        top: 10px;
    }

    .homeBut {
        position: absolute;
        left: 0px;
    }

    .loginBut {
        position: absolute;
        right: 0px;
    }

    .reg {
        cursor: pointer;
        color: #8f9eff;

    }

    .reg:hover {
        color: #409EFF;
    }
</style>
